@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.mkdf-comment-holder {
    @include mkdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .mkdf-comment-holder-inner {
        padding: 35px 0 25px;

        @include phone-landscape {
            padding: 45px 35px 55px;
        }

        @include phone-landscape {
            padding: 45px 10px 55px;;
        }
    }

    .mkdf-comment {
        background-color: #f5f9ff;
        border-radius: 10px;
        box-sizing: border-box;
        padding: 45px 95px 55px 48px;

        @include phone-landscape {
            padding: 45px 35px 55px;;
        }

        &.mkdf-pingback-comment {

            .mkdf-comment-text {
                padding: 0;
            }
        }
    }

    .mkdf-comment-list {
        position: relative;
        padding: 0;
        margin: 0;
        list-style: none;
        z-index: 150;

        li {
            @include mkdfRelativeHolderLayout();
            margin: 25px 0 0;
            padding: 15px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 81px;
        }
    }

    .mkdf-comments-title {
        margin-bottom: 40px;

        > * {
            margin: 0;
        }
    }

    .mkdf-comment-image {
        position: relative;
        top: 10px;
        left: 0;
        width: 120px;
        height: 120px;
        display: block;
        float: left;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .mkdf-comment-text {
        padding: 0 0 0 160px;
        min-height: 120px;

        .mkdf-comment-info {
            @include mkdfRelativeHolderLayout();
            margin: 3px 0 5px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            margin-right: 14px;
            font-size: 13px;
            color: $default-heading-color;
            font-weight: 600;

            &:hover {
                color: $first-main-color;
            }
        }

        .comment-reply-link:before,
        .comment-edit-link:before {
            content: '';
            position: relative;
            top: 1px;
            display: inline-block;
            background-image: url('../img/arrow-right-small.svg');
            width: 26px;
            height: 9px;
            margin-right: 6px;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
        }

        .mkdf-comment-name {
            float: left;
            margin: 0;
        }

        .mkdf-comment-date {
            float: left;
            margin: 0;
            font-size: 12px;
        }

        .mkdf-text-holder p {
            margin: 7px 0 23px;
            letter-spacing: .01em;
        }

        .mkdf-text-holder ul {
            margin: 7px 0 23px;
        }

        .mkdf-text-holder {
            > ul {
                padding-left: 0!important;
            }
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 15px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.mkdf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;

    .mkdf-comment-form-inner {
        padding: 65px 0 70px;
    }

    .comment-reply-title {
        margin: 0 0 37px;
    }

    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .mkdf-comment-holder {

        .mkdf-comment-list {

            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {

    .mkdf-comment-holder {

        .mkdf-comment-list {

            .children {
                padding: 0;
            }
        }

        .mkdf-comment-text {

            .mkdf-comment-date,
            .mkdf-comment-name {
                float: none;
            }

            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {

    .mkdf-comment-holder {

        .mkdf-comment-image {
            float: none;
        }

        .mkdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.mkdf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;

        footer & {
            margin: 0;
        }
    }
}

div.wpcf7-validation-errors,
div.wpcf7-acceptance-missing {
    border-color: $first-main-color;
}

//#submit_comment, // added our button classes in comments.php
//input.wpcf7-form-control.wpcf7-submit, // added our button classes in contact-form-7-confing.php
.post-password-form input[type='submit']{
    @include mkdfButtonDefaultStyle();
    @include mkdfButtonSize();
    @include mkdfButtonSolidColor();

    cursor: pointer;

    &:hover {
        @include mkdfButtonSolidHoverColor();
    }
}

.comment-form-cookies-consent {
    margin-bottom: 44px;

    @include phone-landscape {
        margin: 40px 0;
    }
}

p.form-submit,
.wpcf7-form p {
    margin: 2px 0 0;

    button.mkdf-btn.mkdf-btn-solid {
        background-color: $first-main-color;
    }
}

.mkdf-btn.wpcf7-submit {
    margin-top: 8px;
    overflow: hidden;
    .mkdf-btn-text {
        padding-right: 30px;
        @include mkdfTransition(padding .17s .08s ease-in-out);

        &:before {
            position: absolute;
            content: '';
            width: 26px;
            height: 9px;
            top: calc(50% - 4px);
            left: 0;
            opacity: 0;
            @include mkdfTransform(translateX(17px));
            @include mkdfTransition(transform .2s ease-out, opacity .2s ease-out);
            background-image: url(../img/arrow-white.svg);
        }

        &:after {
            position: absolute;
            content: '';
            right: 0;
            top: calc(50% - 4px);
            width: 26px;
            opacity: 1;
            @include mkdfTransform(translateX(-33px));
            @include mkdfTransition(transform .2s .25s ease-out, opacity .2s .25s ease-out);
            height: 9px;
            background-image: url(../img/arrow-white.svg);
        }
    }

    &:hover {
        .mkdf-btn-text {
            padding-left: 30px;
            padding-right: 0;
            @include mkdfTransition(padding .17s .08s ease-in-out);

            &:before {
                opacity: 1;
                @include mkdfTransform(translateX(33px));
                @include mkdfTransition(transform .2s .25s ease-out, opacity .2s .25s ease-out);
            }

            &:after {
                opacity: 0;
                @include mkdfTransform(translateX(-17px));
                @include mkdfTransition(transform .2s ease-out, opacity .2s ease-out);
            }
        }
    }
}

.mkdf-cf7-dark-arrows .wpcf7-submit {
    .mkdf-btn-text {
        &:before {
            background-image: url(../img/arrow-default.svg);
        }
        &:after {
            background-image: url(../img/arrow-default.svg);
        }
    }
}

#respond textarea,
.mkdf-style-form textarea,
.wpcf7-form textarea {
    display: block;
    padding-bottom: 5px;
    resize: none;
}

#respond {
    #author,
    #email {
        width: 48.9%;

        @include phone-landscape {
            width: 100%;
            margin: 0;
        }
    }

    #author {
        margin-right: 2.2%;
        float: left;
    }
}

.mkdf-two-columns-form-without-space {
    .wpcf7-form .ajax-loader {
        display: none;
    }
}

.mkdf-newsletter-form {
    input[type=email] {
        border-bottom: 1px solid #d9d9d9;
        color: $default-text-color;
        padding-bottom: 0;
        margin-top: 22px;
    }
    .mkdf-btn.mkdf-btn-solid {
        background-color: $first-main-color;
    }
}

.mkdf-cf7-custom-form {
    .wpcf7-form-control {
        &.wpcf7-text,
        &.wpcf7-number,
        &.wpcf7-date,
        &.wpcf7-textarea,
        &.wpcf7-select,
        &.wpcf7-quiz {
            border-bottom: 1px solid #d9d9d9;
            color: $default-text-color;
        }
    }
    .mkdf-btn.mkdf-btn-solid {
        background-color: $first-main-color;
    }
}

span.wpcf7-form-control-wrap.mkdf-select:after {
    font-family: 'ElegantIcons';
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    content: "\43";
    right: 20px;
    top: 14px;
    padding: 0 0 2px;
    position: absolute;
    pointer-events: none;
}

span.wpcf7-form-control-wrap.mkdf-select .wpcf7-form-control.wpcf7-select {
    height: 40px;
    padding: 0;
    cursor: pointer;
    border-top: none;
    border-left: none;
    border-right: none;
}
/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.mkdf-owl-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    &.mkdf-slider-is-moving {
        .owl-item {
            .mkdf-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.mkdf-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include mkdfTransform(translateY($pos));
            }
        }
    }

    &.mkdf-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {

        .owl-prev,
        .owl-next {
            position: absolute;
            top: 50%;
            padding: 0;
            margin: 0;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            z-index: 3;

            @include mkdfTransform(translateY(-50%));
            @include mkdfTransition(color .2s ease-out);

            > span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-size: 40px;
                line-height: 1;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .owl-prev {
            left: 30px;
            color: $first-main-color;

            &:hover {
                color: $third-main-color;
            }
        }

        .owl-next {
            right: 30px;
            color: $third-main-color;

            &:hover {
                color: $first-main-color;
            }
        }
    }

    .owl-dots {
        text-align: center;
        margin: 16px 0 0;

        .owl-dot {
            display: inline-block;
            vertical-align: middle;
            padding: 0;
            margin: 0 12px;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;

            span {
                display: inline-block;
                vertical-align: middle;
                width: 4px;
                height: 4px;
                border-radius: 50%;
                background-color: $default-heading-color;

                @include mkdfTransition(background-color .2s ease-out, border .2s ease-out);
            }

            &:hover,
            &.active {

                span {
                    @include mkdfTransform(scale(2));
                }
            }
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .mkdf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .mkdf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;

                @include mkdfTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.mkdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .mkdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {
    .mkdf-wrapper {
        padding: 0 !important;
    }

    .mkdf-content {
        height: 100vh;
        background-image: url(../img/error-page.jpg);
        background-position: center center;
        background-size: contain;
        background-repeat: no-repeat;
    }

    &.admin-bar {
        .mkdf-content {
            height: calc(100vh - 32px);
        }
    }

    .mkdf-content-inner {
        @include mkdfTableLayout();
    }

    .mkdf-page-not-found {
        @include mkdfTableCellLayout();

        padding: 0 60% 0 20%;
        text-align: left;

        .mkdf-404-title {
            margin: 0;
        }

        .mkdf-404-subtitle {
            margin: 35px 0 0;
        }

        .mkdf-404-text {
            margin: 17px 0 0;
        }

        .searchform {
            margin-top: 30px;
            
            .input-holder {
                background-color: #fff;
                border: 2px solid $default-border-color;
                border-radius: 5px;
                box-sizing: border-box;
                height: 58px;

                input,
                button {
                    background-color: transparent;
                    border: 0;
                    border-radius: 0;
                    box-sizing: border-box;
                    color: $default-heading-color;
                    display: inline-block;
                    float: left;
                    font-family: $default-text-font;
                    font-size: 14px;
                    font-weight: 600;
                    height: 58px;
                    line-height: 56px;
                    outline: 0;
                    position: relative;
                    vertical-align: top;
                    -webkit-appearance: none;
                    letter-spacing: .03em;
                    @include mkdfTransition(color .2s ease-in-out);
                }

                input.search-field {
                    padding: 0 0 2px 28px;
                    width: calc(100% - 58px);

                    @include mkdfPlaceholder {
                        color: inherit;
                    }
                }

                button {
                    background: transparent;
                    color: $default-heading-color;
                    cursor: pointer;
                    line-height: 54px;
                    width: 58px;
                    padding-right: 5px;
                    @include mkdfTransition(color .2s ease-in-out);

                    svg {
                        top: 2px;
                    }

                    &:hover {
                        color: $default-heading-color;
                    }

                    span {
                        display: block;
                        left: 0;
                        line-height: inherit;
                        position: absolute;
                        top: 0;
                        width: 100%;

                        &:before {
                            display: block;
                            line-height: inherit;
                        }
                    }
                }
            }
        }
    }

    @include laptop-landscape-large {
        .mkdf-page-not-found {
            padding: 0 61% 0 12%;
        }
    }

    @include ipad-landscape {
        .mkdf-content {
            height: auto;
            min-height: 100vh;
            margin-top: -70px!important; /* 70px is mobile header height */

            .mkdf-content-inner {
                min-height: 100vh;
            }
        }
        .mkdf-page-not-found {
            padding-left: 10%;
        }
    }

    @include ipad-portrait {
        .mkdf-page-not-found {
            vertical-align: bottom;
            padding: 100px 10%;
        }
    }

    @include phone-landscape {
        .mkdf-page-not-found {
            padding: 70px 10%;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#mkdf-back-to-top {
    position: fixed;
    width: 49px;
    height: 49px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;

        @include mkdfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;

        @include mkdfTransition(opacity 0.3s ease);
    }

    .mkdf-btt-wrapper {
        @include mkdfAbsoluteHolderLayout();
        background-color: $first-main-color;
        border-radius: 100%;
        text-align: center;
        @include mkdfTransition(.25s);

        svg {
            fill: #fff;
        }

        svg:first-child {
            position: absolute;
            height: 26px;
            top: calc(50% - 13px);
            left: calc(50% - 5px);
            opacity: 1;
            @include mkdfTransition(transform .2s ease-out, opacity .2s ease-out);
        }

        svg:last-child {
            position: absolute;
            height: 26px;
            top: calc(50% - 13px);
            left: calc(50% - 5px);
            opacity: 0;
            @include mkdfTransform(translateY(17px));
            @include mkdfTransition(transform .2s ease-out, opacity .2s ease-out);
        }
    }

    &:hover {
        .mkdf-btt-wrapper {
            svg:first-child {
                @include mkdfTransform(translateY(-17px));
                opacity: 0;
            }
            svg:last-child {
                opacity: 1;
                @include mkdfTransform(translateY(0));
            }
        }
    }
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.mkdf-self-hosted-video-holder {
    .mkdf-video-wrap {
        .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}

/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */

/* ==========================================================================
   #Search tweak begin
   ========================================================================== */

// WEBKIT
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
    display: none;
}

// IE
input[type="search"]::-ms-clear {
    display: none;
}

input[type=text]::-ms-clear {
    display: none;
}

/* ==========================================================================
   #Search tweak end
   ========================================================================== */