/* ==========================================================================
   Default WordPress widgets styles - begin
   ========================================================================== */

.widget {
    margin: 0 0 $widget-bottom-margin;
    padding: 0;

    .mkdf-widget-title-holder {
        margin-bottom: $widget-list-item-padding;

        .mkdf-widget-title {
            color: $widget-heading-color;
            margin: 0 0 3px;
        }

        .mkdf-separator {
            border-bottom-width: 3px;
            border-color: $widget-text-color;
            width: 30px;
        }
    }

    ul {
        list-style: none;
        margin: 0;
        padding: 0;

        li {
            display: block;
            line-height: $widget-list-item-height;
            margin: 0;
            padding: $widget-list-item-padding 0;

            &:last-child {
                border: 0;
                padding-bottom: 0;
            }

            a {
                color: $widget-link-color;

                &:hover {
                    color: $widget-link-hover-color;
                }
            }
        }

        .children,
        .sub-menu {
            padding-left: 8px;
            margin-top: $widget-list-item-padding;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with lists and animation on link hover

    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_entries,
    &.widget_nav_menu {
        .mkdf-widget-title {
            margin: 0;
        }

        ul {
            li {
                overflow: hidden;

                a {
                    display: inline-block;
                    vertical-align: middle;
                    overflow: hidden;
                    padding: 0 15px 0 0;
                    position: relative;
                    font-size: 15px;
                    font-weight: 500;
                    transition: all .2s ease;

                    &:before {
                        content: url('../img/arrow-red.svg');
                        left: -30px;
                        position: absolute;
                        width: 25px;
                        height: 9px;
                        transition: all .2s ease;
                    }

                    &:hover {
                        padding-left: 32px;

                        &:before {
                            left: 0;
                            transition: all .2s ease;
                        }
                    }
                }
            }
        }
    }
	
	&.widget_archive,
	&.widget_categories {
		ul li a {
			padding-right: 0;
            color: $default-text-color;
		}
	}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with lists and w/o animation on link hover

    &.widget_recent_comments,
    &.widget_rss {
        .mkdf-widget-title {
            margin-bottom: 0;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // calendar

    #wp-calendar {
        text-align: center;
        width: 100%;

        caption {
            margin: 0 0 $widget-list-item-padding;

        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            background-color: $widget-list-item-background-color;
            color: $widget-text-color;
        }

        td{
            a {
                color: $first-main-color;
            }
        }

        tfoot a {
            color: $widget-link-color;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // rss

    &.widget_rss {

        ul li {
            padding: 10px 0;

            .rsswidget {
                color: $widget-heading-color;
            }
        }

        .rss-widget-icon {
            display: inline-block;
        }

        .rss-date {
            display: block;
            margin-bottom: $widget-list-item-padding;
        }

        cite {
            display: block;
            margin-top: calc(#{$widget-list-item-padding} / 2);
        }
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // search

    .mkdf-searchform {
        .input-holder {
            background-color: transparent;
            border: 2px solid $default-border-color;
            border-radius: 5px;
            box-sizing: border-box;
            height: $widget-search-form-height;

            input,
            button {
                background-color: transparent;
                border: 0;
                border-radius: 0;
                box-sizing: border-box;
                color: $default-heading-color;
                display: inline-block;
                float: left;
                font-family: $default-text-font;
                font-size: 14px;
                font-weight: 600;
                height: $widget-search-form-height;
                line-height: calc(#{$widget-search-form-height} - 2px);
                outline: 0;
                position: relative;
                vertical-align: top;
                -webkit-appearance: none;
                letter-spacing: .03em;
                @include mkdfTransition(color .2s ease-in-out);
            }

            input.search-field {
                padding: 0 0 2px 28px;
                width: calc(100% - #{$widget-search-form-height} - 14px);  //14px same as button right padding.

                @include mkdfPlaceholder {
                    color: $widget-text-color;
                }
            }

            button {
                background: transparent;
                color: $default-heading-color;
                cursor: pointer;
                line-height: 54px;
                width: $widget-search-form-height;
                padding-right: 5px;
                @include mkdfTransition(color .2s ease-in-out);

                svg {
                    top: 2px;
                }

                &:hover {
                    color: $widget-link-hover-color;
                }

                span {
                    display: block;
                    left: 0;
                    line-height: inherit;
                    position: absolute;
                    top: 0;
                    width: 100%;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // combobox

    //&.widget_archive, // to affect dropdown in woo widgets
    //&.widget_categories,
    //&.widget_text {
        select {
            background-color: transparent;
            border-color: $widget-list-item-border-color;
            border-radius: 0;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-size: 15px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            margin-top: $widget-list-item-padding;
            outline: 0;
            padding: 0 0 0 21px;
            position: relative;
            vertical-align: top;
            width: 100%;

            @include mkdfTransition(color .2s ease-in-out);

            option {
                color: $default-text-color;
            }
        }
    //}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // tag cloud

    &.widget_tag_cloud,
    &.widget_product_tag_cloud {
        .mkdf-widget-title-holder {
            margin-bottom: 23px;
        }

        .tagcloud {
            a {
                color: $default-heading-color;
                display: inline-block;
                font-family: $default-text-font;
                font-size: 13px !important;
                font-weight: 500;
                line-height: $widget-tag-cloud-height;
                margin: 0 27px 0 0;
                padding-right: 20px;
                @include mkdfTransition(all .35s cubic-bezier(0.03, 1, 0.58, 1));
                position: relative;

                &:last-child {
                    &:after {
                        display: none;
                    }
                }

                &:after {
                    content: '';
                    width: 24px;
                    height: 1px;
                    background-color: $default-heading-color;
                    position: absolute;
                    right: -18px;
                    top: 50%;
                    transform: translateX(-50);
                }

                &:hover {
                    color: $first-main-color;
                    transition: all .2s ease;
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // text

    &.widget_text {
        img {
            display: block;
        }

        p {
            &:first-child {
                margin-top: 0;
                padding-top: $widget-list-item-padding;
            }

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}