aside.mkdf-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    @include ipad-landscape {
        margin-top: 72px;
    }

    .widget {
        color: $default-text-color;

        &:last-child {
            margin: 0 !important;
        }
    }
}

.wpb_widgetised_column {
    position: relative;

}

aside.mkdf-sidebar,
.wpb_widgetised_column {
    // widgets spacing
    $widget-bottom-margin: 42px;
    $widget-bottom-padding: 15px;
    $widget-inner-padding: 15px;

    // widget color
    $widget-background-color: #fff;

    // list items dimensions
    $widget-list-item-height: 20px;
    $widget-list-item-padding: 5px;

    // list items color
    $widget-list-item-background-color: #ffffff;

    // list items border
    $widget-list-item-border-color: $default-border-color;
    $widget-list-item-border-width: 1px;
    $widget-list-item-border-style: solid;

    // text
    $widget-text-color: inherit;
    $widget-heading-color: $default-heading-color;

    // links
    $widget-link-color: $default-text-color;
    $widget-link-hover-color: $default-heading-color;

    // specific widgets
    $widget-search-form-height: $input-height;
    $widget-tag-cloud-height: 30px;

    @import 'widgets/default-wordpress-widgets-flat';
    @import 'widgets/standalone-widgets';

    .widget {
        .mkdf-widget-title-holder {
            margin-bottom: 11px;
        }
    }
}