footer {
    // widgets spacing
    $widget-bottom-margin: 30px;

    // list items dimensions
    $widget-list-item-height: 20px;
    $widget-list-item-padding: 10px;

    // list items color
    $widget-list-item-background-color: #999999;

    // list items border
    $widget-list-item-border-color: #cccccc;
    $widget-list-item-border-width: 1px;
    $widget-list-item-border-style: solid;

    // text
    $widget-text-color: #cfd3e5;
    $widget-heading-color: #fff;

    // links
    $widget-link-color: #cfd3e5;
    $widget-link-hover-color: $first-main-color;

    // specific widgets
    $widget-search-form-height: $input-height;
    $widget-tag-cloud-height: 30px;

    @import 'widgets/default-wordpress-widgets-flat';
    @import 'widgets/standalone-widgets';
}

footer {
    .textwidget {
        a {
            color: #fff;
            cursor: pointer;

            &:hover {
                color: $second-main-color;
            }
        }
    }

    .widget {
        &.widget_mkdf_twitter_widget {
            .mkdf-widget-title {
                margin-bottom: 13px;
            }
        }
        &.widget_tag_cloud {
            .tagcloud a {
                color: $default-heading-color;
            }
        }
        &.widget_archive,
        &.widget_categories {
            ul li a {
                color: #cfd3e5;
            }
        }
        &.widget_tag_cloud .tagcloud a {
            color: #cfd3e5;

            &:after {
                background-color: #cfd3e5;
            }
        }
        .mkdf-searchform .input-holder button {
            fill: #cfd3e5;
            stroke: #cfd3e5;
        }
    }
}